.input {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.input h3 {
  font-size: 1.2rem;
  margin: 0;
  margin-left: 5px;
  margin-bottom: 10px;
  color: #2e2e2e;
}

.input input {
  outline: none;
  box-sizing: border-box;
  z-index: 10;
  width: 100%;
  font-size: 1.4rem;
  height: 48px;
  color: #493636;
  padding-left: 20px;
  padding-right: 20px;
  background: rgba(70, 62, 40, 0.182);
  border-radius: 11px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(70, 62, 40, 0.3);
}

.input input::placeholder {
  color: rgba(82, 82, 82, 0.426);
}
