@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
.welcome-screen {
  position: absolute;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.welcome-container {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.welcome-container .welcome-box {
  box-sizing: border-box;
  margin-left: 20%;
  min-height: 500px;
  min-width: 700px;
  background: rgba(241, 241, 241, 0.2);
  border-radius: 16px;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(0, 0, 0, 0.104);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 45px;
}

.welcome-container .welcome-box h1 {
  text-align: center;
  font-size: 4.5rem;
  color: #493636;
  margin: 0;
  line-height: 1;
  text-shadow: rgba(0, 0, 0, 0.529) 0px 1px 2px;
}

.welcome-container .welcome-box h1 span {
  margin-top: -20px;
  font-size: 4.2rem;
  color: #fdb417;
  font-weight: 800;
}

.welcome-container .welcome-box div {
  width: 50%;
  display: flex;
  gap: 20px;
  flex-direction: row;
  justify-content: center;
}

.welcome-container .welcome-box div button {
  height: 60px;
  border: none;
  border-radius: 20px;
  min-width: 40%;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  color: white;
  transition: 100ms ease-in-out;
}

.welcome-container .welcome-box div button:nth-child(1) {
  background-color: #fdb417;
}

.welcome-container .welcome-box div button:nth-child(2) {
  background-color: #2b2b2b;
}

.welcome-container .welcome-box div button:hover {
  filter: drop-shadow(0px 1px 2px rgba(0, 0, 0, 0.392));
}

.welcome-container .welcome-box div button:active {
  filter: invert(0.1);
}

@keyframes bigCloudLogin {
  0% {
    transform: translate(20%, -120px);
  }
  50% {
    transform: translate(20%, -110px);
  }
  100% {
    transform: translate(20%, -120px);
  }
}

.cloud-model {
  position: absolute;
  pointer-events: none;
  z-index: 1;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.cloud-model img {
  height: 50%;
  width: 50%;
  object-fit: contain;
  transform: translate(20%, -120px);
  animation: bigCloudLogin    ease-in-out infinite 2s;
}

.map-model {
  position: absolute;
  z-index: -2;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.map-model img {
  position: absolute;
  object-fit: contain;
  pointer-events: none;
}

.map-model img:nth-child(1) {
  margin-top: 16%;
  min-height: 60%;
  width: 80%;
  transform: translate(20%, 0px);
  animation: bigCloud   ease-in-out infinite 3s;
}

.map-model img:nth-child(2) {
  margin-top: 10%;
  height: 20%;
  width: 20%;
  animation: smallCloud   ease-in-out infinite 14s;
  filter: drop-shadow(0px 300px 10px rgba(0, 0, 0, 0.06));
}

@keyframes smallCloud {
  0% {
    transform: translate(-100%, -90%) scale(0.6);
  }
  25% {
  }
  50% {
    transform: translate(-120%, -90%) scale(0.3);
  }
  100% {
    transform: translate(-100%, -90%) scale(0.6);
  }
}

@media (max-width: 1100px) {
  .welcome-container {
    justify-content: center;
  }
  .welcome-container .welcome-box {
    margin-left: 0%;
  }
}

@media (max-width: 780px) {
  .welcome-container .welcome-box {
    min-width: 90%;
  }
}

@media (max-width: 555px) {
  .welcome-container .welcome-box h1 {
    margin-left: 5%;
    margin-right: 5%;
    font-size: 3.5rem;
  }
  .welcome-container .welcome-box h1 span {
    font-size: 3.2rem;
  }
  .welcome-container .welcome-box div button {
    min-width: 100px;
    height: 50px;
    font-size: .9rem;
  }
  .map-model img:nth-child(1) {
    margin-top: 100%;
    min-height: 200px;
    width: 540px;
  }
  .map-model img:nth-child(2) {
    margin-top: 50%;
    min-height: 140px;
    width: 160px;
    filter: drop-shadow(0px 300px 10px transparent);
  }
  .cloud-model img {
    height: 400px;
    width: 300px;
  }
}

@media (max-width: 408px) {
  .welcome-container .welcome-box h1 {
    margin-left: 5%;
    margin-right: 5%;
    font-size: 2.5rem;
  }
  .welcome-container .welcome-box h1 span {
    font-size: 3.2rem;
  }
}
